<template>

  <div class="chatRoom-container">
    <!--     -->
    <div class="chatRoom-top-container">
      <div class="chatRoom-top-data-container">
        <div>
          <!--          <router-link :to="'/schedules/' + scheduleId + '/schedule-items/chat-room-list'">-->
          <!--          <router-link :to="{ name: 'ChatRoomList' }">-->

<!--            <v-img src="@/assets/images/icons/to-room-list.png" width="20px" height="20px"-->
<!--                   class="to-room-list" :inline="true"-->
<!--                   @click="redirectToChatRoomList()"-->
<!--            />-->
        </div>
        <div class="chatRoom-data-image"></div>
        <div class="chatRoom-data-text">
          <div class="room-name">{{ roomData.roomName }}</div>
          <div class="room-member-count-container"></div>
          <v-img src="@/assets/images/icons/member-count.png" width="13px" height="13px" class="room-count" :inline="true" />
          <span class="member-count">{{roomData.count}}</span>
        </div>
      </div>
    </div>
    <!--      -->
    <div class="chatRoom-chatting-container" ref="chatContainer">
      <!--   -->
      <div v-for="(message, index) in messages" :key="index">
        <template v-if="message.dateChanged">
          <div class="date-change">{{ message.date }}</div>
        </template>
        <div v-if="!message.empty" class="chat-message" :class="{ 'sent-message': message.sender === '' || message.sender === this.nickname}">
          <div class="message-sender">{{ message.sender }}</div>
          <div class="message-text">{{ message.message }}</div>
          <div class="message-time">{{ message.time ? formatTime(message.time) : '' }}</div>
        </div>
      </div>
    </div>
    <!--      -->
    <div class="chatRoom-send-container">
      <form @submit.prevent="sendMessage">
        <textarea :value="newMessage" @input="updateMessage" @keydown.enter="sendMessageOnEnter" placeholder=" "></textarea>
        <button type="submit"></button>
      </form>
    </div>
  </div>
</template>


<script>
import {getChatRoomData,getChatMessages,sendChatMessage,readUserInfo} from "@/api/index";
import SockJS from 'sockjs-client';
import Stomp from 'webstomp-client'

export default {
  props: {
    scheduleId: {
      type: String, // Long  Number  
      required: true //  prop  ()
    }
  },
  data(){
    return{
      roomData:{},
      stompClient: null,
      messages: [],
      newMessage:'', //   
      nickname:'',
      currentDate: '',
      id : this.scheduleId,
    };
  },
  methods:{
    redirectToChatRoomList() {
      // ChatRoomList   .
      // this.$router.push(`/schedules/${this.id}/schedule-items/chat-room-list`);
      this.$router.push({ name: 'ChatRoomList', params: { id: this.scheduleId } });
    },

    async getRoomData() {
      try{
        const response = await getChatRoomData(this.id);
        this.roomData =  response.data;
      }catch (error){
        console.error("   :", error);
      }
    },
    async loadChatMessages() {
      let previousDate = '';
      try {
        const response = await getChatMessages(this.id);
        this.messages = response.data;

        this.messages.forEach((message, index) => {
          const messageDate = new Date(message.time).toLocaleDateString();
          if (messageDate !== previousDate) {
            this.messages.splice(index, 0, {
              dateChanged: true,
              date: messageDate,
            });
            previousDate = messageDate;
          }
          //    ''   
          // if (message.sender === this.nickname) {
          //   message.sender = '';
          // }
          //     
          if (!message.message) {
            message.empty = true;
          }
        });
      } catch (error) {
        console.error("   :", error);
      }
    },

    sendMessage() {
      if (this.newMessage.trim() === '') {
        return; //    
      }
      if (this.stompClient && this.stompClient.connected) {
        console.log("");
        const messageData = {
          roomId: this.id,
          sender: this.nickname,
          message: this.newMessage,
          time: new Date(),
        };
        this.stompClient.send(`/app/chat`, JSON.stringify(messageData),{});
      }

      //  
      this.newMessage = '';

      this.$nextTick(() => {
        let chatContainer = this.$refs.chatContainer;

        setTimeout(this.scrollToBottom, 100);
      });
    },

    // websocket 
    connectWebSocket() {
      const socket = new SockJS("http://localhost:8080/chatting"); // WebSocket  URL
      this.stompClient = Stomp.over(socket);

      this.stompClient.connect({}, frame => {
        this.connected = true;
        console.log("WebSocket  ",frame);

        //  
        this.stompClient.subscribe(`/topic/${this.id}`, (message) => {
          console.log("message.body="+message.body);
          //    
          const receivedMessage = JSON.parse(message.body);
          this.messages.push({
            sender: receivedMessage.sender,
            message: receivedMessage.message,
            time: new Date(),
          });
          // sendChatMessage(this.id, this.messages);

        });
      });
      setTimeout(this.scrollToBottom, 100);
    },
    async loadUserInfo(){
      try{
        const userInfo = await readUserInfo();
        this.nickname = userInfo.data.nickname;
      }catch(error){
        console.error("   :", error);
      }
    },

    updateMessage(event) {
      this.newMessage = event.target.value;
      setTimeout(this.scrollToBottom, 100);
    },
    formatTime(timeString) {
      const date = new Date(timeString);
      const hours = date.getHours();
      const minutes = date.getMinutes();

      const period = hours < 12 ? "" : "";
      const formattedHours = hours % 12 === 0 ? 12 : hours % 12;
      const formattedMinutes = minutes < 10 ? `0${minutes}` : minutes;

      return `${period} ${formattedHours}:${formattedMinutes}`;
    },
    sendMessageOnEnter(event) {
      if (event.key === 'Enter' && !event.shiftKey) {
        event.preventDefault(); // Enter     (  )
        this.sendMessage(); // sendMessage  
      }
    },
    // scrollToBottom() {
    //   this.$nextTick(() => {
    //     let chatContainer = this.$refs.chatContainer;
    //     chatContainer.scrollTo({top: chatContainer.scrollHeight, behavior: 'smooth'});
    //   });
    // }
    scrollToBottom() {
      this.$nextTick(() => {
        let chatContainer = this.$refs.chatContainer;
        if (chatContainer.scrollTop === chatContainer.scrollHeight) {
          return; //        
        }
        chatContainer.scrollTo({ top: chatContainer.scrollHeight, behavior: 'smooth' });
      });
    }

  },
  watch: {
    messages() {
      setTimeout(this.scrollToBottom, 100);
    },
    newMessage(){
      setTimeout(this.scrollToBottom, 100);
    }
  },

  mounted() {
    this.loadUserInfo();
    this.loadChatMessages();
    this.getRoomData();
  },
  created() {
    this.connectWebSocket();
  }
};

</script>



<style>
.chatRoom-container {
  /* margin-left: 100px;    */
  width: 370px;
  height: 626px;
  border-radius: 10px;
  background-color: #E5F1FF;
  position: relative; /*       */
}
/*   */
.chatRoom-top-container{
  width: 100%;
  height: 100px;
  border-radius: 10px;
  background-color: #E5F1FF;
}
.to-room-list{
  margin-right: 10px;
}
.chatRoom-top-data-container{
  padding: 20px;
  display: flex;
}
.chatRoom-data-image {
  width: 57px;
  height: 57px;
  border-radius: 25px;
  background-color: #FAED7D;
}
.chatRoom-data-text{
  flex: 1;
  margin-left: 16px;
  text-align: left;
}
.room-name {
  font-weight: bold;
  font-size: 20px;
}
.room-member-count-container{
  margin-top: 7px;
}
.member-count{
  margin-left: 5px;
  font-size: 17px;
}
/*    () */
.chatRoom-chatting-container {
  flex: 1; /*        */
  height: 430px;
  overflow-y: scroll;
  margin-bottom: 10px;
}

.chatRoom-chatting-container::-webkit-scrollbar {
  width: 10px;
}

.chatRoom-chatting-container::-webkit-scrollbar-thumb {
  background-color: #DADADA;
  border-radius: 10px;
}
.chatRoom-chatting-container::-webkit-scrollbar-track {
  background-color: #E5F1FF;
  border-radius: 10px;
}

.date-change {
  text-align: center;
  font-size: 14px;
  color: #999;
  margin: 10px 0;
}
.chat-message{
  text-align: left;
  margin-left: 20px;
  margin-right: 15px;
  margin-bottom: 10px; /*     . */
}
.sent-message {
  text-align: right;
}
.message-sender {
  font-weight: bold;
}
.message-time{
  font-size: 13px;
  color: #999;
}
/*     */
.chatRoom-send-container {
  position: absolute; /*    */
  bottom: 0px; /*  0px    */
  left:0;
  width:100%;
  height: 80px;
  background-color: #F1F7FF;
  border-radius: 0px 0px 10px 10px;
}
textarea {
  width: 80%;
  height: 100%; /*  100%    */
  padding: 10px; /*      */
  box-sizing: border-box; /*      */
  border: none; /*   */
  resize: none; /*    */
  font-size: 14px; /*     */
  outline: none;
}

button {
  height: 100%; /*  100%    */
  background-color: #F1F7FF; /*     */
  color: white; /*     */
  border: none;
  border-radius: 10px;
  padding: 10px 20px; /*     */
  font-size: 14px; /*     */
}

</style>