<template>
  <div class="chatRoom-list-container">
    <h2> </h2>
    <div>
      <ul>
        <li v-for="(room, index) in chatRooms" :key="room.id">
          <router-link :to="'/chat/' + room.id">
            <div class="chatRoom-data-container">
              <div class="chatRoom-data-image"></div>
              <div class="chatRoom-data-text">
                <span class="room-name">{{ room.roomName }}</span>
                <span class="latest-message">{{ truncateMessage(room.latestMessage, 20) }}</span>
              </div>
            </div>
          </router-link>
          <!--     -->
          <hr v-if="index < chatRooms.length - 1">
        </li>
      </ul>
    </div>
  </div>
</template>


<script>
import {getChatRooms} from "@/api/index";
export default {
  data() {
    return {
      chatRooms: [], // API    
    };
  },
  mounted() {
    this.loadChatRooms(); //      
  },
  methods: {
    async loadChatRooms() {
      try {
        const response = await getChatRooms();
        const data = await response.data;
        this.chatRooms = data;

      } catch (error) {
        console.error("   :", error);
      }
    },
    truncateMessage(message, maxLength) {
      if (message.length > maxLength) {
        return message.substring(0, maxLength) + "...";
      }
      return message;
    },
  },
};
</script>

<style scoped>
h2 {
  padding: 30px;
  margin-left: 10px;
  text-align: left;
}
.chatRoom-list-container {
  /* margin-left: 100px;   */
  width: 370px;
  height: 626px;
  border-radius: 10px;
  background-color: #E5F1FF;
  overflow-y: scroll;
}
.chatRoom-list-container::-webkit-scrollbar {
  width: 10px;
}

.chatRoom-list-container::-webkit-scrollbar-thumb {
  background-color: #DADADA; /*    */
  border-radius: 10px;
}
.chatRoom-list-container::-webkit-scrollbar-track {
  background-color: #E5F1FF; /*    */
  border-radius: 10px;
}
.chatRoom-data-container {
  display: flex;
  align-items: center;
  margin-right: 10px;
}
.chatRoom-data-image {
  margin-left: 25px;
  width: 57px;
  height: 57px;
  border-radius: 25px;
  background-color: #FAED7D;
}
.chatRoom-data-text {
  flex: 1;
  margin-left: 20px;
  text-align: left;
}
hr {
  border: 1px solid #ccc; /*   . */
  margin: 10px 0; /*     . */
}
.room-name {
  font-weight: bold;
  font-size: 18px;
}
.latest-message {
  display: block; /*    . */
  font-size: 16px; /*   . */
}
</style>