<template>
    <div class="likes-user-container">
        <span class="highlight-text"><strong> </strong></span>
        <!--  -->
        <div class="mt-5" v-show="!showResultTable">  .</div>
        <div class="search-results-container">
            <table class="search-results" v-show="showResultTable">
                <thead>
                <tr>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(user, index) in this.userLikedByMe" :key="user.id" class="user-info">
                    <td>{{ user.nickname }}</td>
                    <td>{{ user.email }}</td>
                    <td>
                        <button class="action-button" @click="unLikeUser(user.id, user)" v-if="!user.isUnLiked">
                            
                        </button>
                        <div class="action-done" v-if="user.isUnLiked"> </div>
                    </td>
                </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import {unLikeUser, readUserLikedByMe} from '@/api/index.js';

export default {
    name: "LikedByMe",
    async created() {
        try {
            const response = await readUserLikedByMe();
            this.userLikedByMe = response.data;
            console.log(response)
        } catch (error) {
            console.log(" :", error.response.data)
        }
    },
    data() {
        return {
            userLikedByMe: [],
            isUnLiked: false,
        };
    },
    computed: {
        showResultTable() {
            return this.userLikedByMe.length > 0
        },
    },
    methods: {
        async unLikeUser(userId, user) {
            try {
                console.log(` \"${userId}.${user.nickname}\"  `)
                console.log(` : isUnLiked ${user.isUnLiked}`)
                const {data} = await unLikeUser(userId);
                user.isUnLiked = true;
                console.log(data);

            } catch (error) {
                if (error.response && error.response.status === 400) {
                    alert("   .");
                    user.isLiked = true;
                } else {
                    alert(error.response.message)
                }
                console.error("  :", error);
            }
        },
    },
};
</script>

<style scoped>

th, td {
    text-align: center;
    border: none;
    padding: 3px 15px;
}

th {
    background-color: #FFFFFF;
    position: sticky;
    top: 0;
}

.highlight-text {
    font-size: 20px;
    background: linear-gradient(to top, #FFE866 50%, transparent 50%);
}

.search-results-container {
    margin-top: 20px;
    max-width: 600px;
    min-width: 500px;
    max-height: 300px;
    overflow-y: auto;
    /*background-color: #42b983;*/
}

.search-results-container::-webkit-scrollbar {
    width: 10px;
}

.search-results-container::-webkit-scrollbar-thumb {
    background-color: #FFE866; /*    */
    border-radius: 10px;
}

.search-results-container::-webkit-scrollbar-track {
    background-color: beige; /*    */
    border-radius: 10px;
}

.search-results {
    width: 100%;
    border-collapse: collapse;
}

.search-results td {
    padding: 8px;
}

.action-button {
    font-size: 11px;
    color: #2F3438;
    width: 45px;
    height: 27px;
    display: inline-block;
    border-radius: 10px;
    line-height: 30px;
    cursor: pointer;
    text-align: center;
    border: none;
    background-color: #FAED7D;
}

.action-done {
    font-size: 11px;
    color: #2F3438;
    width: 45px;
    height: 27px;
    display: inline-block;
    border-radius: 10px;
    line-height: 30px;
    cursor: pointer;
    text-align: center;
    border: none;
    background-color: #E5F1FF;
}
</style>
