<template>
    <v-container class="signUp-container" style="max-width: 500px">
        <v-layout align-center row wrap>
            <v-col cols="12" class="vertical-center">
                <v-alert v-if="isError" type="error">
                    {{ errorMsg }}
                </v-alert>
                <v-card>
                    <v-toolbar color=#C4DFFF style="text-align: center;">
                        <v-toolbar-title style="text-align: center; font-size: 20px; color: #FFFFFF">
                               
                        </v-toolbar-title>
                    </v-toolbar>
                    <div class="pa-5 mt-3">
                        <v-form ref="form" v-model="valid" lazy-validation style="min-width: 400px">
                            <v-text-field
                                    v-model="formData.email"
                                    label="e-mail"
                                    :rules="emailRules"
                                    :error-messages="emailErrors"
                                    required
                                    @input="checkDuplicateEmail"
                            ></v-text-field>
                            <v-text-field
                                    v-model="formData.nickname"
                                    label="nickname"
                                    :counter="12"
                                    :rules="nameRules"
                                    :error-messages="nicknameErrors"
                                    required
                                    @input="checkDuplicateNickname"
                            ></v-text-field>

                            <v-text-field
                                    v-model="formData.password"
                                    :type="show ? 'text' : 'password'"
                                    label="password"
                                    counter="8"
                                    :rules="[rules.required, rules.min, rules.max, rules.password]"
                            ></v-text-field>

                            <v-text-field
                                    v-model="formData.passwordCheck"
                                    :type="show ? 'text' : 'password'"
                                    label="password check"
                                    counter="8"
                                    :rules="[passwordConfirmationRule]"
                            ></v-text-field>
                            <div class="d-flex flex-row align-center font-adjust">
                                <div class="ml-2 flex-row align-center">
                                    <v-icon
                                            size="19"
                                            color="grey"
                                            :class="show ? 'mdi mdi-eye' : 'mdi mdi-eye-off'"
                                            @click="show = !show"
                                    ></v-icon>
                                    <span class="ml-1" @click="show = !show"> </span>
                                </div>
                                <div class="ml-auto mr-1">
                                    <v-btn flat
                                           depressed
                                           large
                                           block
                                           class="mb-1"
                                           @click="reset"
                                    >
                                    </v-btn>
                                </div>
                            </div>
                            <v-btn flat
                                   color="#C4DFFF"
                                   depressed
                                   large
                                   block
                                   class="mt-4 mb-4"
                                   @click="register(formData)"
                                   style="font-size: 17px; color: #FFFFFF;"
                            >
                            </v-btn>
                        </v-form>
                    </div>
                </v-card>
            </v-col>
        </v-layout>
    </v-container>
</template>

<script>
import RegisterObj from "../models/registerObj"

export default {
    name: "signUp",
    data: () => ({
            formData: new RegisterObj("", "", "", ""),
            valid: false,
            isEmailUnique: undefined,
            isNicknameUnique: undefined,
            isError: false,
            errorMsg: "",
            emailErrors: [],
            nicknameErrors: [],
            show: false,
            rules: {
                required: (value) => !!value || " .",
                password: v => !!(v || '').match(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*(_|[!@#$%^&])).+$/) ||
                    "  , (!@#$%^&) .",
                min: (v) => v.length >= 8 || "8  .",
                max: (v) => v.length <= 20 || "20  .",
            }
        }
    ),

    computed: {
        passwordConfirmationRule() {
            return () =>
                this.formData.password === this.formData.passwordCheck || "  .";
            console.log(" :" + this.formData.password === this.formData.passwordCheck)
        },
        emailRules() {
            return [
                (v) => !!v || "e-mail .",
                (v) => /.+@.+\..+/.test(v) || "e-mail  .",
            ];
        },
        nameRules() {
            return [
                (v) => !!v || "nickname .",
                (v) => (v && v.length <= 12) || "  ."
            ];
        },
    },

    methods: {
        goToLogin() {
            console.log("goToLogin: login  ")
            this.$router.push({name: "Login"})
        }
        ,
        register(RegisterObj) {
            //   
            if ( // null, undefined, false,  
                !this.formData.email ||
                !this.formData.nickname ||
                !this.formData.password ||
                !this.formData.passwordCheck
            ) { // 
                this.isError = true
                this.errorMsg = ", ,   ."
                return
            }
            //    , vaild true 
            this.validate()
            if (this.valid) {
                console.log("vaild:", this.valid)
                this.axios.post("/users/register", RegisterObj)
                    .then(() => {
                        alert(" .")
                        this.goToLogin()
                    })
                    .catch((err) => {
                        if (err.response) {
                            this.isError = true
                            this.errorMsg = err.response.data.message
                        }
                    });
            }
        }
        ,
//   
        async checkDuplicateEmail() {
            try {
                const response = await this.axios.post(`/users/check/email/${this.formData.email}`);
                if (response.data == false) {
                    this.isEmailUnique = true; //  true , false  unique
                    this.emailErrors = [];
                } else {
                    this.isEmailUnique = false; //  true , false  unique
                    this.emailErrors.push("  email.");
                }
                // console.log(`: ${this.formData.email},  : ${response.data ? '' : ''}`);
            } catch (error) {
                console.error("email  : " + error);
            }
            console.log(`isEmailUnique: ${this.isEmailUnique}`);
        }
        ,
//   
        async checkDuplicateNickname() {
            try {
                const response = await this.axios.post(`/users/check/nickname/${this.formData.nickname}`);
                if (response.data == false) {
                    this.isNicknameUnique = true; //  true , false  unique
                    this.nicknameErrors = [];
                } else {
                    this.isNicknameUnique = false; //  true , false  unique
                    this.nicknameErrors.push("  .");
                }
                // console.log(`nickname: ${this.formData.nickname},  : ${response.data ? '' : ''}`);
            } catch (error) {
                console.error("nickname  : " + error);
            }
            console.log(`isNicknameUnique: ${this.isNicknameUnique}`);
        }
        ,
        validate() {
            this.$refs.form.validate().then((validation) => {
                console.log("check Valid: " + "isEmailUnique: " + this.isEmailUnique + ", isNicknameUnique: " + this.isNicknameUnique)
                console.log("validate:", validation)
                if (validation && this.isEmailUnique && this.isNicknameUnique) {
                    this.valid = true;
                } else {
                    this.valid = false;
                }
            });
        }
        ,
        reset() {
            this.$refs.form.reset()
            this.isError = false
        }
        // ,
        // resetValidation() {
        //     this.$refs.form.resetValidation()
        // }
    }
}
</script>
<style>

.align-center {
    align-items: center;
}

.font-adjust {
    font-size: 15px;
    color: #455A64;
}

</style>