<template>
  <div>
    <span class="highlight-text"><strong> </strong></span>
    <!--   -->
    <form @submit.prevent="searchUsers">
      <input v-model="keyword" type="text" placeholder="   ">
      <button class="search-button" type="submit">
        
      </button>
    </form>

    <!--    -->
    <div class="search-results-container">
      <table  class="search-results" v-show="showResultTable">
        <thead>
        <tr v-show="showTableHeader">
          <th></th>
          <th></th>
          <th></th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(user, index) in searchResults" :key="user.id" class="user-info">
          <td>{{ user.nickname }}</td>
          <td>{{ user.email }}</td>
          <td>
            <button class="invite-button" @click="inviteUser(user.nickname,user)" v-if="!user.isInvited">
              
            </button>
            <div v-if="user.isInvited"> </div>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
import {  findInvitationList, inviteUserToSchedule  } from '@/api/index.js';

export default {
  props:["scheduleId"],
  data() {
    return {
      keyword: "",
      searchResults: [],
      isLoading: false,
      showTableHeader:false,
      showTable:false,
      isInvited:false,

    };
  },
  computed:{
    showResultTable(){
      return this.searchResults.length > 0 || this.isLoading;
    },
  },
  methods: {
    async searchUsers() {
      try {
        const response = await findInvitationList(this.scheduleId,this.keyword);
        this.searchResults = response.data;
        this.isLoading = false;
        this.showTableHeader = this.searchResults.length > 0;
        if (this.searchResults.length===0)
          alert("  .   .");

      } catch (error) {
        console.error(" :", error);
        alert("  .   .");
        this.isLoading = false;
      }
    },
    async inviteUser(username,user) {
      try {
        const response = await inviteUserToSchedule(this.scheduleId,username);
        alert(" .");
        user.isInvited=true;

      } catch (error) {
        if (error.response && error.response.status === 400) {
          alert("  .");
          user.isInvited=true;
        } else {
          alert(" .");
        }
        console.error("    :", error);
      }
    },


  },
};
</script>

<style scoped>

.highlight-text {

  font-size: 20px;
  background: linear-gradient(to top, #FFE866 50%, transparent 50%);
}
th, td {
  text-align: center;
  border: none;
}
.search-results-container {
  margin-top: 20px;
  max-width: 500px;
  max-height: 150px;
  overflow-y: scroll;
  //margin: 0 auto; /*    */
}
.search-results-container::-webkit-scrollbar {
  width: 10px;
}

.search-results-container::-webkit-scrollbar-thumb {
  background-color: #FFE866; /*    */
  border-radius: 10px;
}
.search-results-container::-webkit-scrollbar-track {
  background-color: beige; /*    */
  border-radius: 10px;
}

.search-results {
  width: 100%;
  border-collapse: collapse;
}

.search-results td {
  padding: 8px;
}
.search-button{
  font-size: 14px;
  width: 40px;
  height: 27px;
  display: inline-block;
  border-radius: 10px;
  line-height: 30px;
  cursor: pointer;
  text-align: center;
  border: none;
  background-color: #EAEAEA;
}

.invite-button {
  font-size: 14px;
  color: #2F3438;
  width: 40px;
  height: 28px;
  display: inline-block;
  border-radius: 10px;
  line-height: 30px;
  cursor: pointer;
  text-align: center;
  border: none;
  background-color: #FAED7D;
}
</style>
