<template>
    <div class="item_review">
        <h2> </h2>
        <textarea v-model="reviewText" placeholder=" "></textarea>
        <div class="rating">
            <span v-for="star in 5" :key="star" @click="setRating(star)" :class="{ active: star <= currentRating }">&#9733;</span>
        </div>
        <button @click="submitReview"> </button>
    </div>
</template>

<script>
import axios from "axios";
import { itemReview } from "@/api"; // itemReview  API    .

export default {
    name: "item-review",
    props: {
        itemId: {
            type: String, // itemId      
            required: true, //     
        },
    },
    data() {
        return {
            reviewText: '',
            currentRating: 0,
            itemReviews: [],
        };
    },
    methods: {
        setRating(rating) {
            this.currentRating = rating;
            console.log('rating:', rating)
        },
        async submitReview() {
            if (!this.reviewText || this.currentRating === 0) {
                alert("   .");
                return;
            }


            //const ratingAsNumber = this.currentRating; // : ONE(1) 1, TWO(2) 2, ...

            const reviewData = {
                content: this.reviewText,
                rating: this.currentRating, //   
            };
            console.log('Data:', reviewData)

            try {
                const { data } = await itemReview(this.itemId, reviewData);
                alert("  .");
            } catch (error) {
                console.error("  :", error);
                alert("    .");
            }
        },
    },
};
</script>

<style scoped>
