<template>
    <div class="sidebar_main">
        <div class="main_title"> </div>
        <div class="my_page">
            <v-alert v-if="isError" type="error">
                {{ errorMsg }}
            </v-alert>
            <v-form ref="form" v-model="valid" lazy-validation class="userInfoEdit_form">
                <div class="my_page_profile_image mt-5">
                    <v-img :src="profileImgPreview" cover width="250px" height="250px" class="rounded-circle"
                           :inline="true"></v-img>
                    <label for="file" class="my_page_profile_change mt-3">  </label>
                    <input type="file" name="file" id="file" accept="image/*" @change="uploadProfileImage">
                    <!--                    <div class="my_page_profile_delete" @click="removeProfileImage">  </div>-->
                </div>
                <div class="my_page_profile_form ml-3">
                    <div class="my_page_edit">
                        <div class="my_page_input_label"></div>
                        <div class="readonly_data">{{ this.email }}</div>
                    </div>
                    <div class="my_page_edit">
                        <div class="my_page_input_label"></div>
                        <v-text-field class="v-text-field-custom"
                                      v-model="formData.nickname"
                                      variant="outlined"
                                      :counter="12"
                                      :rules="nameRules"
                                      :error-messages="nicknameErrors"
                                      @input="checkDuplicateNickname"
                        ></v-text-field>
                    </div>

                    <div class="my_page_edit">
                        <div class="my_page_input_label introduce"> </div>
                        <v-textarea class="v-text-field-custom"
                                    v-model="formData.profileContent"
                                    variant="outlined"
                                    :rows="3"
                                    :counter="35"
                                    :rules="[rules.contentRules]"
                        ></v-textarea>
                    </div>

                    <div class="my_page_edit">
                        <div class="my_page_input_label introduce"></div>
                        <v-text-field class="v-text-field-custom"
                                      v-model="formData.profileLocation"
                                      variant="outlined"
                                      :rules="[rules.locationRules]"
                        ></v-text-field>

                    </div>
                </div>
            </v-form>
            <div class="my_page_button_wrap">
                <v-btn flat class="my_page_button" @click="submitForm"> </v-btn>
            </div>

        </div>
    </div>
</template>

<script>

import {updateUserInfo, readUserInfo} from "@/api";

export default {
    name: "UserInfoEdit",

    async created() {
        try {
            const {data} = await readUserInfo();
            console.log(data)
            //  profileImage      
            this.email = data.email;
            this.formData.nickname = data.nickname;
            this.formData.profileContent = data.content;
            this.formData.profileLocation = data.location;
            this.profileImgPreview = data.profileImage;
            this.presentNickname = data.nickname;
        } catch (error) {
            console.log(" :", error.response.data)
        }
    },
    data() {
        return {
            formData:
                {
                    nickname: ' ',
                    profileContent: '',
                    profileLocation: '',
                    isImageDefault: undefined,
                    profileImage: []
                },
            valid: undefined,
            email: ' email',
            profileImgPreview: '/img/default-profile.png',
            presentNickname: '',
            isNicknameUnique: true,
            nicknameErrors: [],
            isError: false,
            errorMsg: '',
            rules: {
                contentRules: (v) => v.length <= 35 || "35  .",
                locationRules: (v) => v.length <= 20 || "20  .",
            }
        }
    },
    computed: {
        nameRules() {
            return [
                (v) => !!v || " .",
                (v) => (v && v.length <= 12) || "    .",
            ];
        },
    },
    methods: {
        goToMain() {
            // this.$router.go(0);
            this.$router.push('/')
        }
        ,
        async submitForm() {
            if (
                !this.formData.nickname
            ) {
                this.isError = true
                this.errorMsg = " ."
                return;
            }
            this.validate()
            console.log("valid:", this.valid)
            if (this.valid) {
                try {
                    console.log("formData:", this.formData)

                    const dto = {
                        nickname: this.formData.nickname,
                        profileContent: this.formData.profileContent,
                        profileLocation: this.formData.profileLocation,
                        isImageDefault: this.formData.isImageDefault
                    }
                    const userData = new FormData()
                    userData.append("dto", new Blob([JSON.stringify(dto)], {type: "application/json"}));
                    console.log("image:", this.formData.profileImage)
                    userData.append('image', this.formData.profileImage);

                    const {data} = await updateUserInfo(userData);
                    alert(data.message);
                    this.goToMain()
                } catch
                    (error) {
                    console.log(error.response.data)
                    this.isError = true
                    this.errorMsg = error.response.data.message
                }
            }
        }
        ,

        uploadProfileImage(e) {
            let imageFile = e.target.files;
            this.formData.profileImage = imageFile[0] //   
            this.profileImgPreview = URL.createObjectURL(imageFile[0]);
            console.log(" :", this.formData.profileImage)
        }
        ,
        removeProfileImage() {
            //   ,    
            this.profileImgPreview = "/img/default-profile.png";
            this.formData.profileImage = [];
            this.formData.isImageDefault = true;
            console.log(" :", this.formData.profileImage)
            console.log("isImageDefault:", this.formData.isImageDefault)
        }
        ,

//   
        async checkDuplicateNickname() {
            //   
            if (this.formData.nickname === this.presentNickname) {
                console.log(` : ${this.presentNickname}`)
                return;
            }
            //   
            if (this.formData.nickname === null) {
                console.log(" ")
                return;
            }
            //   
            if (this.formData.nickname != this.presentNickname) {
                try {
                    console.log(`: nickname: ${this.formData.nickname}`)
                    const response = await this.axios.post(`/users/check/nickname/${this.formData.nickname}`);
                    if (response.data == false) {
                        this.isNicknameUnique = true; //  true , false  unique
                        this.nicknameErrors = [];
                    } else {
                        this.isNicknameUnique = false; //  true , false  unique
                        this.nicknameErrors.push("  .");
                    }
                    // console.log(` : ${response.data ? '' : ''}`);
                } catch (error) {
                    console.error("nickname  : " + error);
                }
                console.log(`isNicknameUnique: ${this.isNicknameUnique}`);
            } else {
                console.log(` : ${this.isNicknameUnique}`);
            }
        }
        ,
        validate() {
            this.$refs.form.validate().then((validation) => {
                console.log("check Valid: " + "isNicknameUnique: " + this.isNicknameUnique)
                console.log("validate:", validation)
                if (validation && this.isNicknameUnique) {
                    this.valid = true;
                } else {
                    this.valid = false;
                }
            });
        }
        ,
    }
}
</script>

<style scoped>


.main_title {
    font-size: 22px;
    font-weight: 700;
    text-align: left;
    padding-bottom: 20px;
}

.userInfoEdit_form {
    display: flex;
    flex-direction: row;
    min-width: 500px;
}

.my_page_profile_image {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-right: 40px;
}

.rounded-circle {
    border: 1px solid #ccc;
}

.my_page_profile_change {
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}


.my_page_profile_delete {
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
    color: #D3728E;
}

.my_page_profile_image input {
    display: none;
}

.my_page_profile_form {
    margin-top: 28px;
    text-align: left;
}

.my_page_edit {
    margin-bottom: 28px;
    display: flex;
    gap: 40px;
}

.my_page_input_label {
    font-size: 16px;
    font-weight: bold;
    display: inline-block;
    width: 78px;
    margin-top: 12px;
}

.readonly_data {
    font-size: 16px;
    display: inline-block;
    margin-top: 12px;
}

.my_page_edit input {
    width: 480px;
    font-size: 16px;
    border-radius: 8px;
    border: 2px solid #DADADA;
    padding: 5px 9px 5px;
}

.my_page_edit textarea {
    width: 480px;
    font-size: 16px;
    border-radius: 8px;
    border: 2px solid #DADADA;
    resize: none;
    height: 100px;
    line-height: 20px;
}

.edit_warn {
    font-size: 15px;
    color: #D3728E;
    height: 15px;
    margin-top: 9px;
    text-align: left;
}

.my_page_button_wrap {
    text-align: center;
}

.my_page_button {
    border: none;
    margin: 40px auto 30px;
    width: 198px;
    height: 42px;
    display: inline-block;
    background-color: #99C7FF;
    font-size: 17px;
    font-weight: bold;
    color: #FFF;
    cursor: pointer;
}

</style>