<template>
  <div class="sidebar_main">
    <div class="content-title">
      <strong>  </strong>
    </div>
    <br>
    <!--       -->
    <div v-if="invitations.length === 0">
         .
    </div>
    <!--         -->
    <table class="invitation-table" v-else>
      <thead>
      <tr>
        <th></th>
        <th></th>
        <th> </th>
        <th> </th>
        <th> </th>
      </tr>
      </thead>
      <tbody>
      <tr v-for="(invitation, index) in invitations" :key="index" class="invitation-item">
        <td>{{ index + 1 }}</td>
        <td>{{ invitation.scheduleHost }}</td>
        <td>{{ invitation.scheduleTitle }}</td>
        <td>{{ invitation.invitedTime }}</td>
        <td>
          <button class="accept-button" @click="acceptInvitation(invitation)" v-if="!invitation.decision"></button>
          <button class="reject-button" @click="rejectInvitation(invitation)" v-if="!invitation.decision"></button>
          <div v-if="invitation.accepted && invitation.decision"> </div>
          <div v-if="!invitation.accepted && invitation.decision"> </div>
        </td>
      </tr>
      </tbody>
    </table>
  </div>
</template>


<script>
import { readInvitations, acceptInvitation, rejectInvitation } from '@/api/index.js';

export default {
  data() {
    return {
      invitations: [ //     
      {
        accepted: false,
        decision: false
      }
      ],
    };
  },
  mounted() {
    this.fetchInvitations();
  },
  methods: {
    //   
    fetchInvitations() {
      readInvitations()
          .then((response) => {
            this.invitations = response.data;
          })
          .catch((error) => {
            console.error('     :', error);
          });
    },
    //   
    acceptInvitation(invitation) {
      acceptInvitation(invitation.scheduleId, invitation.matesId)
          .then((response) => {
            invitation.accepted = true;
            invitation.decision = true;
          })
          .catch((error) => {
            console.error('    :', error);
          });
    },
    //   
    rejectInvitation(invitation) {
      rejectInvitation(invitation.scheduleId, invitation.matesId)
          .then((response) => {
            invitation.accepted = false;
            invitation.decision = true;
          })
          .catch((error) => {
            console.error('    :', error);
          });
    },
  },
};
</script>

<style scoped>
.sidebar_main {
  width: 937px;
}

.content-title{
  font-size: 25px;
}
.invitation-table {
  width: 75%;
  border-collapse: collapse;
  margin: 0 auto;
}

th, td {
  border: 1px solid #ccc;
  padding: 8px;
  border: none;

}

th {
  //background-color: #f2f2f2;
}
td{
  //text-align: left;
}

td:nth-child(4) {
  width:200px;
}
button {
  margin-left: 10px;
  margin-right: 10px; /*     */
  width: 50px;
  height: 30px;
  //display: inline-block;
  border-radius: 10px;
  line-height: 23px;
  cursor: pointer;
  text-align: center;
  //border:none;
}
.accept-button{
  background-color: #FFE866;
  color: #616161;
}
.reject-button{
  background-color: #EAEAEA;
}
</style>