<template>
    <v-app>
        <v-navigation-drawer class="side_bar">
            <v-list>
                <v-list-item class="content_title"></v-list-item>
                <v-list-item><a @click="$router.push('/boards')">  </a></v-list-item>
                <v-list-item><a @click="$router.push('/schedule-list')">  </a></v-list-item>
            </v-list>
        </v-navigation-drawer>

        <v-main class="board">
            <ul>
                <div class="wrapper">
                    <label for="title"> :  </label>
                    <input id="title" type="text" v-model="title">
                </div>
                <EditorTiptap class="editor" v-model="content" :max-limit="1000" />
                <button class="submitBtn" @click="submitForm"></button>
            </ul>
        </v-main>
    </v-app>
</template>

<script>
import { createBoard, readBoards } from '@/api/index';
import EditorTiptap from '../components/EditerTiptap.vue';
export default {
  name: 'BoardCreate',
  components: {
    EditorTiptap
  },
  data() {
    return {
      title: '',
      content: ''
    };
  },
  methods: {
    async submitForm() {
      try {
        //  
        const boardData = {
          title: this.title,
          content: this.content,
        };
        const response = await createBoard(boardData);
        console.log(response.data);
        this.goToBoardDetails(response.data);

        } catch (error) {
          //   
          console.log(error.response.data);
          this.logMessage = error.response.data;
        }
    },
    goToBoardDetails(boardId) {
      this.$store.dispatch('updateBoardId', boardId);
      this.$router.push(`/board-details`);
    }
  }
}
</script>

<style scoped>
    .editor{
        width: 900px;
    }

  .board {
      margin-top: 15px;
      margin-left: 10px;
  }

  .wrapper {
      display: grid;
      grid-template-columns: auto 2fr;
      align-items: center;
      font-size: 22px;
      font-weight: 700;
      margin-bottom: 20px;
      //column-gap: 10px; /*      */
  }

  #title {
      margin-left: 10px;
      border: 1px solid #000; /*     */
      padding: 5px; /*      */
      border-radius: 3px;
      width: 840px;
  }

  .submitBtn {
      margin-left: 10px;
      margin-top: 10px;
      border: 1px solid #000;
      padding: 5px;
      border-radius: 3px;
      display: block;
  }

  .content_title {
      display: inline-block;
      margin-right: 10px;
      font-size: 22px;
      font-weight: 700;
      text-align: right;
      padding-bottom: 20px;
  }

  .side_bar {
      margin-top: 130px;
  }

</style>
